<template>
  <transition name="slide">
  <div class="download-warp" v-if="show">
    <div class="download-close" @click="close"></div>
    <div class="download-body">
      <img v-if='!showSG' src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/uplus_logo.png" alt="">
      <img v-else src="https://cdn09.ehaier.com/shunguang/H5/www/img/zjzg_logo.png" class="cr" alt="">
      <div>打开{{showSG?'智家掌柜App':'海尔智家APP'}} <p>{{showSG?'你的掌上智慧店铺':'重新定义智慧新家'}}</p></div>
    </div>
    <div id="download-btn" class="download-btn" @click="open">立即打开</div>
  </div>
  </transition>
</template>

<script>
import utils from '@/utils/utils'
const showShunguang = utils.isIhaierApp() || utils.isIMaker() // ihaier或者爱创客环境下打开顺逛
export default {
  data() {
    return {
      show: true,
      showSG: showShunguang,
    }
  },
  watch: {
    $route() {
      if (!showShunguang) {
        utils.openInstall('download-btn')
      }
    }
  },
  mounted() {
    if (!showShunguang) {
      utils.openInstall('download-btn')
    }
  },
  methods: {
    open() {
      if (showShunguang) {
        utils.openShunguang()
      }
    },
    close() {
      this.show = false
      this.$emit('hidden')
    }
  }
}
</script>

